import axios from 'axios';
//import { axios } from "./axios-mock";

export const getAPIParams = (user, action) => {
  switch (action) {
    case "create":
      return {
        task: "create",
        name: user["name"],
        email: user["email"],
        username: user["username"],
        password: user["password"],
      };
    case "update":
      return {
        task: "update",
        id: user["id"],
        name: user["name"],
        email: user["email"],
        username: user["username"],
        password: user["password"],
      };
    case "delete":
      return {
        task: "delete",
        id: user["id"],
      };
    case "readall":
      return {
        task: "readall",
      };
    default:
      return {
        task: "readall",
      };
  }
};

export const AXIOS_CONFIG = {
  headers: {
    "Content-Type": "application/x-www-form-urlencoded",
  },
};

const createRequest = (user, action) =>
  axios
    .post(window.__API_URL__, getAPIParams(user, action), AXIOS_CONFIG)
    .then(({ data }) => data);

export const makeAxiosRequest = (user) => ({
  create: () => createRequest(user, "create"),
  update: () => createRequest(user, "update"),
  delete: () => createRequest(user, "delete"),
  readall: () => createRequest(user, "readall"),
});


Date.prototype.timeNow = function () {
  return (
    (this.getHours() < 10 ? "0" : "") +
    this.getHours() +
    ":" +
    (this.getMinutes() < 10 ? "0" : "") +
    this.getMinutes() +
    ":" +
    (this.getSeconds() < 10 ? "0" : "") +
    this.getSeconds()
  );
 
};
 