import React, { useEffect } from "react";
import { User, DEFAULT_USER } from "./User";
import { Logger, useLines } from "./Logger";
import { makeAxiosRequest } from "../tools/utils";
//import Table  from 'react-bootstrap/Table';
//import { Table } from 'react-bootstrap';
import { Table, Thead, Tbody, Tr, Th } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';

const UsersCRUD = () => {
  const [users, setUsers] = React.useState([]);
  const { lines, addLogLine } = useLines();

  useEffect(() => {
    makeAxiosRequest({})
      .readall()
      .then((users) => {
        setUsers(users);
        addLogLine(`Read ${users.length} users`);
      });
  }, [addLogLine]);

  const onUpdate = React.useCallback(
    async (user) => {
      if (user.id) {
        try {
          user = await makeAxiosRequest(user).update();
          setUsers((users) => users.map((u) => (u.id === user.id ? user : u)));
          addLogLine(`Updated user ${user.id}`);
        } catch (e) {
          setUsers((users) =>
            users.map((u) => (u.id === user.id ? { ...u } : u))
          );
          addLogLine(`Error updating user ${user.id}`, true);
        }
      } else {
        user = await makeAxiosRequest(user).create();
        setUsers((users) => [...users, user]);
        addLogLine(`Created user ${user.id}`);
      }
    },
    [addLogLine]
  );

  const onDelete = React.useCallback(
    async (user) => {
      user = await makeAxiosRequest(user).delete();
      setUsers((users) => users.filter((u) => u.id !== user.id));
      addLogLine(`Deleted user ${user.id}`);
    },
    [addLogLine]
  );

  const renderedUsers = React.useMemo(
    () =>
      users.map((user) => (
        <User
          user={user}
          key={user.id}
          onUpdate={onUpdate}
          onDelete={onDelete}
        />
      )),
    [users, onUpdate, onDelete]
  );

  return (
    <div className="container">
      <div className="crud">
        <CrudTable>
          <User user={DEFAULT_USER} onUpdate={onUpdate}></User>
        </CrudTable>
        <CrudTable includeHeader={true}>{renderedUsers}</CrudTable>
        <div className="logger">
          <Logger lines={lines}></Logger>
        </div>
      </div>
    </div>

  );
};

const CrudTable = ({ includeHeader, children }) => (

  <div>
    <Table className="user-table">

      {includeHeader && (
        <Thead>
          <Tr>
            <Th>ID</Th>
            <Th>Name</Th>
            <Th>Email</Th>
            <Th>Username</Th>
            <Th>Password</Th>
            <Th>Actions</Th>
          </Tr>
        </Thead>
      )}
      <Tbody>{children}</Tbody>
    </Table>
  </div>

);

export default UsersCRUD;
