import * as React from "react";
import { Tr, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';

export const DEFAULT_USER = {
  id: "",
  name: "",
  email: "",
  username: "",
  password: "",
};

export const User = React.memo(({ user, onUpdate, onDelete }) => {
  const [localUser, setUser] = React.useState({ ...user });
  const [wasEdited, setWasEdited] = React.useState(false);

  React.useEffect(() => {
    setUser({ ...user });
  }, [user]);

  const onChange = React.useCallback(({ target: { value, name } }) => {
    setWasEdited(true);
    setUser((prev) => ({ ...prev, [name]: value }));
  }, []);

  const updateUser = React.useCallback(() => {
    onUpdate(localUser);
    setWasEdited(false);
    if (!user.id) {
      setUser(DEFAULT_USER);
    }
  }, [onUpdate, localUser, user.id]);

  const deleteUser = React.useCallback(() => {
    onDelete(localUser);
  }, [localUser, onDelete]);

  return (
    
    <Tr
      key={user.id || "createUser"}
      className={wasEdited && user.id ? "edited" : ""}
    >
      {user.id ? <Td>{localUser.id}</Td> : <Td>New user</Td>}
      <Td>
        <input
          type="text"
          name="name"
          value={localUser.name}
          onChange={onChange}
        />
      </Td>
      <Td>
        <input
          type="text"
          name="email"
          value={localUser.email}
          onChange={onChange}
        />
      </Td>
      <Td>
        <input
          type="text"
          name="username"
          value={localUser.username}
          onChange={onChange}
        />
      </Td>
      <Td>
        <input
          type="password"
          name="password"
          value={localUser.password}
          onChange={onChange}
        />
      </Td>
      {user.id && (
        <Td>
          <button disabled={!wasEdited ? "disabled" : ""} onClick={updateUser}>
            Update
          </button>
          <button onClick={deleteUser}>Delete</button>
        </Td>
      )}
      {!user.id && (
        <Td>
          <button disabled={!wasEdited ? "disabled" : ""} onClick={updateUser}>
            Create
          </button>
        </Td>
      )}
    </Tr>
      
  );
});
