import React from "react";

const LOGGER_LINES_LENGTH = 10;

export const useLines = () => {
  const [lines, setLines] = React.useState([]);
  const addLogLine = React.useCallback(
    (line, isError = false) =>
      setLines((lines) =>
        [...lines, { line: `${new Date().timeNow()} ${line}`, isError }].slice(
          -LOGGER_LINES_LENGTH
        )
      ),
    []
  );
  return { lines, addLogLine };
};

export const Logger = React.memo(({ lines }) => {
  return (
    <ul>
      {lines.map(({ line, isError }, index) => (
        <li key={index} className={isError ? "error" : ""}>
          <code>{line}</code>
        </li>
      ))}
    </ul>
  );
});
